//  Предварительная загрузка изображений для лучшего пользовательского опыта
export const preloadImages = async (): Promise<void> => {
  const imageUrls = [
    'https://images.unsplash.com/photo-1636466497217-26a8cbeaf0aa?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwxfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800',
    'https://images.unsplash.com/photo-1453733190371-0a9bedd82893?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwyfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800',
    'https://images.unsplash.com/photo-1635070041409-e63e783ce3c1?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwzfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800'
  ];
  
  imageUrls.forEach(url => {
    const img = new Image();
    img.src = url;
  });
};

// Получение изображения на основе типа неравенства
export const getTypeImage = (inequalityType: string): string => {
  switch (inequalityType) {
    case 'Квадратное параметрическое неравенство':
      return 'https://images.unsplash.com/photo-1635070041409-e63e783ce3c1?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwzfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800';
    case 'Линейное параметрическое неравенство':
      return 'https://images.unsplash.com/photo-1636466497217-26a8cbeaf0aa?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwxfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800';
    case 'Рациональное параметрическое неравенство':
      return 'https://images.unsplash.com/photo-1453733190371-0a9bedd82893?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwyfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800';
    default:
      return 'https://images.unsplash.com/photo-1636466497217-26a8cbeaf0aa?ixid=M3w3MjUzNDh8MHwxfHNlYXJjaHwxfHxtYXRoZW1hdGljcyUyMGVxdWF0aW9ucyUyMGJsYWNrYm9hcmQlMjBmb3JtdWxhc3xlbnwwfHx8fDE3NDM4NjY4ODB8MA&ixlib=rb-4.0.3&fit=fillmax&h=600&w=800';
  }
};
 