import React, { useEffect, useState } from 'react';
import { useTheme } from '../hooks/useTheme';

interface InequalityVisualizerProps {
  inequality: string;
  parameter: string;
  currentValue: number;
}

export const InequalityVisualizer: React.FC<InequalityVisualizerProps> = ({
  inequality,
  parameter,
  currentValue
}) => {
  const { theme } = useTheme();
  const [rendered, setRendered] = useState(false);

  useEffect(() => {
    // Render LaTeX
    setRendered(true);
  }, [inequality, parameter, currentValue, theme]);

  return (
    <div className="overflow-x-auto">
      <div className="latex-container">
        {inequality} ({parameter}={currentValue})
      </div>
      {!rendered && <div>Loading...</div>}
    </div>
  );
};
 