//  Интерфейс для структуры решения
export interface Solution {
  type: string;
  steps: { explanation: string; latex: string }[];
}

// Define a Step type for cleaner code
export type Step = {
  explanation: string;
  latex: string;
};

// Основная функция анализа неравенства
export async function analyzeInequality(inequality: string, parameter: string): Promise<Solution> {
  try {
    // Базовая проверка
    if (!inequality.includes('<') && !inequality.includes('>') && !inequality.includes('=')) {
      throw new Error('Пожалуйста, введите корректное неравенство с <, >, ≤ или ≥');
    }
    
    // Используем локальный анализ
    const result = fallbackAnalysis(inequality, parameter);
    return result;
  } catch (error) {
    console.error('Ошибка при анализе неравенства:', error);
    throw new Error(`Не удалось проанализировать: ${(error as Error).message}`);
  }
}

// Определение типа неравенства
function determineInequalityType(inequality: string, parameter: string): string {
  const hasParameter = inequality.includes(parameter);
  const isQuadratic = inequality.includes('^2') || inequality.includes('²');
  const isFraction = inequality.includes('/') || inequality.includes('÷');
  const isLinear = !isQuadratic && !isFraction;
  
  if (isQuadratic && hasParameter) {
    return 'Квадратное параметрическое неравенство';
  } else if (isLinear && hasParameter) {
    return 'Линейное параметрическое неравенство';
  } else if (isFraction && hasParameter) {
    return 'Рациональное параметрическое неравенство';
  } else {
    return 'Базовое параметрическое неравенство';
  }
}

// Резервный анализ
function fallbackAnalysis(inequality: string, parameter: string): Solution {
  const type = determineInequalityType(inequality, parameter);
  
  let steps: Step[] = [];
  
  // Добавляем базовые шаги анализа
  steps.push({
    explanation: `Анализируем параметрическое неравенство: ${inequality} с параметром ${parameter}`,
    latex: `${inequality}`
  });
  
  // Определяем ключевые компоненты
  if (type === 'Квадратное параметрическое неравенство') {
    steps = steps.concat(solveQuadraticParametricInequality(inequality, parameter));
  } else if (type === 'Линейное параметрическое неравенство') {
    steps = steps.concat(solveLinearParametricInequality(inequality, parameter));
  } else if (type === 'Рациональное параметрическое неравенство') {
    steps = steps.concat(solveRationalParametricInequality(inequality, parameter));
  } else {
    steps = steps.concat(solveBasicParametricInequality(inequality, parameter));
  }
  
  return {
    type,
    steps
  };
}

// Решение квадратного параметрического неравенства
function solveQuadraticParametricInequality(inequality: string, parameter: string): Step[] {
  let steps: Step[] = [];
  
  // Шаг 1: Идентифицируем форму
  steps.push({
    explanation: `Это квадратное параметрическое неравенство с параметром ${parameter}.`,
    latex: `ax^2 + bx + c < 0`
  });
  
  // Шаг 2: Объясняем анализ дискриминанта
  steps.push({
    explanation: `Для решения этого неравенства нам нужно найти значения ${parameter}, при которых квадратное выражение имеет действительные корни.`,
    latex: `D = b^2 - 4ac`
  });
  
  // Шаг 3: Объясняем, как параметр влияет на дискриминант
  steps.push({
    explanation: `Параметр ${parameter} влияет на коэффициенты квадратного выражения, что в свою очередь влияет на дискриминант.`,
    latex: `Анализируем как ${parameter} влияет на D`
  });
  
  // Шаг 4: Объясняем случаи на основе дискриминанта
  steps.push({
    explanation: `Рассмотрим три случая:`,
    latex: `Случай 1: D > 0 (два действительных корня)`
  });
  
  steps.push({
    explanation: `Когда дискриминант положительный, квадратное уравнение имеет два действительных корня, r₁ и r₂.`,
    latex: `Если a > 0, решение: x ∈ (r_1, r_2)`
  });
  
  steps.push({
    explanation: `Если старший коэффициент положительный и неравенство вида < 0, то решением будет интервал между корнями.`,
    latex: `Если a < 0, решение: x ∈ (-∞, r_1) ∪ (r_2, ∞)`
  });
  
  // Шаг 5: Случай нулевого дискриминанта
  steps.push({
    explanation: `Когда дискриминант равен нулю, квадратное уравнение имеет один повторяющийся корень r.`,
    latex: `Случай 2: D = 0 (один повторяющийся корень)`
  });
  
  steps.push({
    explanation: `В этом случае решение обычно пусто для неравенства вида < 0 (так как квадратное выражение только касается оси x).`,
    latex: `Для < 0 при D = 0, обычно решений нет`
  });
  
  // Шаг 6: Случай отрицательного дискриминанта
  steps.push({
    explanation: `Когда дискриминант отрицательный, квадратное уравнение не имеет действительных корней.`,
    latex: `Случай 3: D < 0 (нет действительных корней)`
  });
  
  steps.push({
    explanation: `В этом случае знак квадратного выражения постоянен для всех x. Решение зависит от знака старшего коэффициента.`,
    latex: `Если a > 0 и неравенство вида < 0, решений нет`
  });
  
  steps.push({
    explanation: `Если старший коэффициент отрицательный и неравенство вида < 0, решением будут все действительные числа.`,
    latex: `Если a < 0 и неравенство вида < 0, решение: x ∈ ℝ`
  });
  
  // Шаг 7: Заключение
  steps.push({
    explanation: `Чтобы найти конкретные значения ${parameter}, при которых неравенство верно для некоторых значений x, нам нужно решить условия, при которых применяется нужный нам случай.`,
    latex: `Решение зависит от значений ${parameter}`
  });
  
  if (inequality.includes('x^2 + ax - 3 < 0')) {
    // Конкретное решение для примера x^2 + ax - 3 < 0
    steps.push({
      explanation: `Для данного неравенства имеем a = 1, b = ${parameter}, c = -3`,
      latex: `x^2 + ${parameter}x - 3 < 0: a = 1, b = ${parameter}, c = -3`
    });
    
    steps.push({
      explanation: `Дискриминант: Δ = ${parameter}² - 4(1)(-3) = ${parameter}² + 12`,
      latex: `D = ${parameter}^2 + 12 > 0`
    });
    
    steps.push({
      explanation: `Поскольку Δ > 0 для всех значений ${parameter}, квадратное уравнение всегда имеет два действительных корня.`,
      latex: `r_1 = (-${parameter} - √(${parameter}^2 + 12))/2, r_2 = (-${parameter} + √(${parameter}^2 + 12))/2`
    });
    
    steps.push({
      explanation: `Поскольку a = 1 > 0 и неравенство вида < 0, решением будет интервал между корнями.`,
      latex: `x ∈ (r_1, r_2) для всех значений ${parameter}`
    });
  }
  
  return steps;
}

// Решение линейного параметрического неравенства
function solveLinearParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `Это линейное параметрическое неравенство с параметром ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `Для линейного неравенства с параметром ${parameter} нам нужно проанализировать, как параметр влияет на коэффициент и свободный член.`,
      latex: `ax + b < 0`
    },
    {
      explanation: `Нам нужно изолировать x, чтобы понять, когда неравенство удовлетворяется.`,
      latex: `x < -b/a (когда a > 0)`
    },
    {
      explanation: `Если коэффициент при x отрицательный, нам нужно изменить знак неравенства на противоположный.`,
      latex: `x > -b/a (когда a < 0)`
    },
    {
      explanation: `Параметр ${parameter} может влиять как на коэффициент при x, так и на свободный член.`,
      latex: `Анализируем как ${parameter} влияет на границу решения -b/a`
    },
    {
      explanation: `Существует также особый случай, когда коэффициент при x равен нулю.`,
      latex: `Если коэффициент при x = 0, получаем условие на сам ${parameter}`
    },
    {
      explanation: `Решение будет представлять собой диапазон значений для x, который зависит от конкретного значения ${parameter}.`,
      latex: `Решение принимает разные формы в зависимости от значения ${parameter}`
    }
  ];
}

// Решение рационального параметрического неравенства
function solveRationalParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `Это рациональное параметрическое неравенство с параметром ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `Для рациональных неравенств сначала нужно найти, когда знаменатель равен нулю, так как эти точки являются неопределенными.`,
      latex: `Найдем значения, где знаменатель = 0`
    },
    {
      explanation: `Затем находим, когда числитель равен нулю, так как это пересечения рациональной функции с осью x.`,
      latex: `Найдем значения, где числитель = 0`
    },
    {
      explanation: `Эти критические точки разделяют числовую прямую на интервалы, где рациональное выражение сохраняет знак.`,
      latex: `Критические точки: нули числителя и знаменателя`
    },
    {
      explanation: `Для каждого интервала проверяем, удовлетворяет ли рациональное выражение неравенству.`,
      latex: `Проверяем каждый интервал, используя тестовую точку`
    },
    {
      explanation: `Рациональное выражение отрицательно, когда числитель и знаменатель имеют противоположные знаки.`,
      latex: `P(x)/Q(x) < 0 когда P(x) и Q(x) имеют противоположные знаки`
    },
    {
      explanation: `Параметр ${parameter} может влиять на нули как числителя, так и знаменателя.`,
      latex: `Разные значения ${parameter} могут приводить к разным критическим точкам`
    },
    {
      explanation: `Полное решение будет зависеть от конкретных значений ${parameter} и от того, как они влияют на критические точки.`,
      latex: `Решение: x ∈ (интервалы в зависимости от ${parameter})`
    }
  ];
}

// Решение базового параметрического неравенства
function solveBasicParametricInequality(inequality: string, parameter: string): Step[] {
  return [
    {
      explanation: `Анализируем параметрическое неравенство с параметром ${parameter}.`,
      latex: `${inequality}`
    },
    {
      explanation: `Сначала идентифицируем все члены, содержащие параметр ${parameter}.`,
      latex: `Идентифицируем члены с ${parameter}`
    },
    {
      explanation: `Затем выделяем коэффициенты при различных степенях x.`,
      latex: `Выделяем коэффициенты при различных степенях x`
    },
    {
      explanation: `Для каждого значения параметра ${parameter} определяем, когда неравенство выполняется.`,
      latex: `Для каждого значения ${parameter} определяем условия неравенства`
    },
    {
      explanation: `Множество решений зависит от конкретного значения параметра ${parameter}.`,
      latex: `Решение зависит от конкретного значения ${parameter}`
    }
  ];
}

